import React, { createRef, useEffect, useRef, useState } from "react";
// styles
import styles from "./index.module.scss";
// internal components
import FormErrorMessage from "components/UI/common/ErrorMessage";
import CustomController from "components/UI/common/Controller";
import { useForm, useFieldArray } from "react-hook-form";
import NormalButton from "components/UI/common/NormalButton/NormalButton";
import { sleep } from "Constants/helperFunctions";
import Toggle from "components/UI/common/Toggle";
import DefineRoomType from "./DefineRoomType";
import CommonLabelInput from "components/UI/common/CommonLabelInput";
import {
  addPropertyCreateRoomType,
  addPropertyUpdateStayType,
  getDefineRoomTypeList,
} from "service/StayDetails";
import { numberInput } from "helpers/validation";
import { useDispatch, useSelector } from "react-redux";
import { getStayTypeDetails } from "../../../../service/StayDetails";
import { Toast } from "Constants/toast";
import { setTabDirty, setTabStatus } from "redux/slice/basicSlice";
import debounce from "lodash/debounce";
import { getTabStatusDetails } from "service/TabDetails";

const StayDetailCreationForm = ({
  handleNextTab,
  showPreview,
  activeTab,
  isPreviewScreen,
  childSaveDraftRef
}) => {
  const {
    register,
    handleSubmit,
    getValues,
    setValue,
    reset,
    control,
    resetField,
    watch,
    formState: { errors, isDirty },
    trigger,
    clearErrors,
  } = useForm({
    //mode: "all",
    mode: "onChange",
    defaultValues: {
      shortStayDay: 30,
      shortStay: false,
      longStay: false,
      isDraft: false,
    },
  });

  const childRef = useRef();
  const dispatch = useDispatch();
  const propertyId = useSelector((state) => state.basic.propertyId);
  // const propertyId = "652414b6aedac4d4e61d835a";
  const [stateupdated, setStateupdate] = useState(false);
  //const [roomList, setRoomList] = useState(roomData);
  const [saveDraft, setSaveDraft] = useState(false);
  const [isDisabled, setIsDisabled] = useState(false);

  const [stayTypeApiCalled, setStayTypeApiCalled] = useState(false);

  const options = [
    { value: 1, label: "Option 1" },
    { value: 2, label: "Option 2" },
    // Add more variants as needed
  ];

  useEffect(() => {
    dispatch(setTabDirty(isDirty))
  }, [isDirty])

  useEffect(() => {
    if (propertyId) {
      getstayTypeDetailsData(propertyId);
    }
  }, [propertyId]);

  const checkNextTabsCompletion = async () => {
    try {
      const response = await getTabStatusDetails(propertyId);
      if (response?.data?.length) {
        return ["Room Packages", "Service Amenities"].every(
          (tab) =>
            response?.data?.findIndex(
              (a) => a.tabName === tab && a?.tabStatus == "COMPLETED"
            ) >= 0
        );
      }
      return true;
    } catch (e) {
      console.log("e :>> ", e);
    }
  };

  const handleScroll = () => {
    // window.scrollTo(0, document.body.scrollHeight);
  };

  const getstayTypeDetailsData = async (propertyId) => {
    try {
      const response = await getStayTypeDetails(propertyId);

      if (response.status === 200) {
        const data = response.data?.data;

        let shortStayData =
          (data &&
            data.stayTypes &&
            data?.stayTypes?.find((item) => item.name == "SHORT_STAY")) ||
          {};
        let longStayData =
          (data &&
            data.stayTypes &&
            data?.stayTypes?.find((item) => item.name == "LONG_STAY")) ||
          {};

        reset((prevValue) => ({
          ...prevValue,
          shortStay: shortStayData?.isEnabled || false,
          shortStayDay: shortStayData?.stayDuration || 30,
          longStay: longStayData?.isEnabled || false,
          longStayDay: 0,
        }));
        childRef?.current?.getdefineTypeDetailsData?.();
        //setValue("setCheckInTimeHour", data?.checkInTime);
        // getdefineTypeDetailsData();
      } else {
        Toast({ type: "error", message: response?.data?.message });
      }
    } catch (e) {
      console.log("e :>> ", e);
    }
  };

  const handleSaveDraft = async (e) => {
    //setValue("isDraft", true);
    await setSaveDraft(true);
    handleSubmit(onsubmit, (arg1) => { })(e);
    // if (!Object.keys(errors).length) {
    //   handleNextTab()
    // }
  };

  const onsubmit = async (data) => {
    //await sleep(500);

    setIsDisabled(true);
    try {
      let body = {
        propertyId: propertyId,
        stayTypes: [
          {
            name: "SHORT_STAY",
            stayDuration: data.shortStayDay,
            stayDurationUnit: "DAY",
            isEnabled: data?.shortStay,
          },
        ],
      };
      if (data?.longStay) {
        body?.stayTypes.push({
          name: "LONG_STAY",
          stayDuration: 0,
          stayDurationUnit: "DAY",
          isEnabled: data?.longStay,
        });
      }

      const newOne = data?.rooms.find((c) => c._id && c._id !== "");

      let tableForm = {
        propertyId: propertyId,
        isDraft: getValues("isDraft"),
        types: [],
        deletedArray: data?.deletedArray,
      };

      let tableBody =
        data?.rooms && data?.rooms?.length > 0
          ? data?.rooms?.map((item) => ({
            _id: item._id,
            roomType: item?.roomType,
            roomCategories: [
              {
                category: "Retail",
                shortStay:
                  item.chargeType == "Corporate" || item.shortStay === false
                    ? false
                    : item?.shortStayRetail,
                longStay:
                  item.chargeType == "Corporate" || item.longStay === false
                    ? false
                    : item.longStayRetail,
              },
              {
                category: "Corporate",
                shortStay:
                  item.chargeType == "Retail" || item.shortStay === false
                    ? false
                    : item?.shortStayCorporate,
                longStay:
                  item.chargeType == "Retail" || item.longStay === false
                    ? false
                    : item.longStayCorporate,
              },
            ],
          }))
          : [];
      tableForm.types = tableBody;
      let isCreate = newOne ? false : true;
      const response = await addPropertyUpdateStayType(body);
      if (response.status == 200) {
        const result = await addPropertyCreateRoomType(tableForm, isCreate);
        dispatch(setTabDirty(false));
        dispatch(setTabStatus());
        setIsDisabled(false);
        if (result.status == 200) {
          setIsDisabled(false);
          if (!getValues("isDraft")) {
            if (isPreviewScreen) {
              const isNextTabsCompleted = await checkNextTabsCompletion();
              if (!isNextTabsCompleted) handleNextTab();
              else handleNextTab(activeTab, false, isPreviewScreen);
            } else handleNextTab();
          } else {
            propertyId && getstayTypeDetailsData(propertyId);
          }
        }
      } else {
        setIsDisabled(false);
      }
    } catch (e) {
      // handle error
      setIsDisabled(false);
    }
  };
  const handleShortStayupdate = (value, keyname) => {
    //{ keyname == "shortStay" ? setShortStayFlag(value) : setLongStayFlag(value) }
    let stateData = getValues();
    let data = getValues("rooms") || [];
    let roomData = data;
    roomData.forEach((option) => {
      option[keyname] = value;
      if (option.roomTypeItem) {
        option.roomTypeItem.forEach((option) => {
          option[keyname] = value;
        });
      }
    });

    reset({
      ...stateData,
      rooms: roomData,
    });
  };
  const handleshortStayValue = () => {
    let stateData = getValues();
    reset({
      ...stateData,
      // rooms: roomData,
      shortStayDay: "30",
    });
  };

  return (
    <div className={styles.stayDetailForm}>
      {showPreview ? (
        <>
          {" "}
          <div className={`${styles.contentPart} px-3`}>
            <div className={styles.stayType}>
              <div className="d-flex py-2">
                {Boolean(getValues("shortStay")) === true && (
                  <div className="row">
                    <div className={`col-12 preview-label`}>Short Stay </div>
                    <div className={`col-12 preview-value1`}>
                      {`less than / equal to ${getValues("shortStayDay")} ${Number(getValues("shortStayDay")) > 1 ? "days" : "day"
                        }`}
                    </div>
                  </div>
                )}
                {Boolean(getValues("longStay")) === true && (
                  <div className="row">
                    {" "}
                    <div className={`col-12 preview-label`}>Long Stay</div>{" "}
                    <div className={` preview-value1`}>On monthly basis</div>
                  </div>
                )}
              </div>
            </div>
            <div>
              <DefineRoomType

                ref={childRef}
                control={control}
                clearErrors={clearErrors}
                //handleSubmit={handleSubmit}
                resetField={resetField}
                useFieldArray={useFieldArray}
                errors={errors}
                register={register}
                roomList={getValues("rooms") ?? []}
                getValues={getValues}
                deletedArray={getValues("deletedArray") ?? []}
                reset={reset}
                shortStayFlag={getValues("shortStay")}
                longStayFlag={getValues("longStay")}
                setValue={setValue}
                propertyId={propertyId}
                watch={watch}
                stayTypeApiCalled={stayTypeApiCalled ? true : false}
                trigger={trigger}
                handleScroll={handleScroll}
                showPreview={showPreview}
              />
            </div>
          </div>
          <hr />
          <div className={`d-flex justify-content-end ${styles.btn} py-2 px-2`}>
            <div className={styles.btnwidth}>
              <NormalButton
                className={"draftBtn"}
                label={"Edit Details "}
                id={"editBtn"}
                onClick={(e) => {
                  handleNextTab(activeTab, true);
                }}
              />
            </div>
            <div className={styles.btnwidth}>
              <NormalButton
                className={"submitBtn"}
                label={"Submit"}
                id="submitBtn"
                //onClick={handleNextTab}
                onClick={debounce((e) => {
                  handleNextTab();
                }, 800)}
              />
            </div>
          </div>
        </>
      ) : (
        <>
          <div className={`${styles.contentPart} px-3`}>
            <div className="new-section py-2">
              <div className={styles.basicTitle}>
                <h3>Define Stay Type</h3>
              </div>

              <div className={styles.stayType}>
                <div className="d-flex py-2">
                  <div className={`${styles.stayStyles}  B4-Medium col-md-1`}>
                    Stay Type{" "}
                  </div>
                  <div
                    className={`${styles.stayStyles} d-flex B4-Medium col-md-1`}
                  >
                    {" "}
                    Stay Duration<span className="text-danger">*</span>
                  </div>
                </div>

                <div className="d-flex py-1">
                  <div className="B3-Semi-bold pt-2 fs-15 col-md-1">
                    Short Stay:{" "}
                  </div>
                  <div className="B3-Medium col-md-3 gap-2">
                    <CommonLabelInput
                      className="login_input"
                      type="number"
                      onKeyDown={numberInput}
                      value={watch("shortStayDay")}
                      name={"shortStayDay"}
                      defaultValue={watch("shortStayDay")}
                      placeholder="Enter short stay days"
                      errors={errors}
                      prefixText="less than / equal to"
                      suffixText="days"
                      register={register("shortStayDay", {
                        required: true,
                        pattern: /^[0-9\b]+$/,
                        min: 1,
                        max: 45,
                      })}
                      width="30%"
                      disabled={
                        getValues("shortStay") == true &&
                          getValues("longStay") == false
                          ? false
                          : true
                      }
                    />
                    <div className="px-1">
                      {" "}
                      <FormErrorMessage
                        error={errors.shortStayDay}
                        messages={{
                          required: "Short stay days are required",
                          min: "Short stay should be a minimum 1 day",
                          max: "Short stay should be a maximum 45 days",
                        }}
                      />
                    </div>
                  </div>
                  <div className="B3-Medium pt-3 col-md-12 gap-1">
                    <CustomController
                      name="shortStay"
                      control={control}
                      defaultValue={false} // Set the initial value
                      rules={{
                        required:
                          getValues("longStay") === false ? true : false,
                      }}
                      messages={{
                        required: "Atleast one stay type should be enabled",
                      }}
                      render={({ field: { onChange, value } }) => (
                        <Toggle
                          name="shortStay"
                          value={value}
                          onChange={(value) => {
                            onChange(value);
                            setValue("shortStay", value);
                            setValue("shortStayDay", 30);
                            handleshortStayValue(value);
                            handleShortStayupdate(value, "shortStay");
                            handleShortStayupdate(value, "shortStayRetail");
                            handleShortStayupdate(value, "shortStayCorporate");
                          }}
                        />
                      )}
                    />
                  </div>
                </div>
                <div className="d-flex py-4">
                  <div className="B3-Semi-bold col-md-1">Long Stay: </div>
                  <div className="B3-Medium col-md-3"> On monthly basis </div>
                  <div className="col">
                    <div className="d-flex">
                      <CustomController
                        name="longStay"
                        control={control}
                        defaultValue={getValues("longStay") || false} // Set the initial value
                        rules={{
                          required:
                            getValues("shortStay") == false ? true : false,
                        }}
                        render={({ field: { onChange, value } }) => (
                          <Toggle
                            name="longStay"
                            value={value}
                            onChange={(value) => {
                              onChange(value);
                              setValue("longStay", value);
                              setValue("shortStayDay", 30);
                              handleShortStayupdate(value, "longStay");
                              handleShortStayupdate(value, "longStayRetail");
                              handleShortStayupdate(value, "longStayCorporate");
                            }}
                          />
                        )}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <DefineRoomType
                  ref={childRef}
                  control={control}
                  clearErrors={clearErrors}
                  //handleSubmit={handleSubmit}
                  resetField={resetField}
                  useFieldArray={useFieldArray}
                  errors={errors}
                  register={register}
                  roomList={getValues("rooms") ?? []}
                  getValues={getValues}
                  deletedArray={getValues("deletedArray") ?? []}
                  reset={reset}
                  shortStayFlag={getValues("shortStay")}
                  longStayFlag={getValues("longStay")}
                  setValue={setValue}
                  propertyId={propertyId}
                  watch={watch}
                  stayTypeApiCalled={stayTypeApiCalled ? true : false}
                  trigger={trigger}
                  handleScroll={handleScroll}
                  showPreview={showPreview}
                />
              </div>
            </div>
          </div>
          <hr />          
          <div className={`d-flex justify-content-end mx-3`}>
            {" "}
            <FormErrorMessage
              icon={true}
              error={errors?.shortStay && errors?.longStay}
              messages={{
                required: "Atleast one stay type should be enabled",
              }}
            />
          </div>
          <div className={`d-flex justify-content-end ${styles.btn} py-2 px-2`}>
            <div className={styles.btnwidth}>
              <NormalButton
                innerref={childSaveDraftRef}
                className={"draftBtn"}
                label={isPreviewScreen ? "Cancel" : "Save as Draft"}
                //id={"draftBtn"}
                onClick={debounce((e) => {
                  e.preventDefault();
                  if (isPreviewScreen) {
                    handleNextTab(activeTab, false, isPreviewScreen);
                    return;
                  }
                  setValue("isDraft", true);
                  setTimeout(() => {
                    handleSaveDraft(e);
                  }, 500);
                }, 800)}
                disabled={isDisabled}
              />
            </div>
            <div className={styles.btnwidth}>
              <NormalButton
                type={"submit"}
                className={"submitBtn"}
                label={isPreviewScreen ? "Save Changes" : "Save & Proceed"}
                id="submitBtn"
                //onClick={handleNextTab}
                onClick={debounce((e) => {
                  e.preventDefault();
                  setValue("isDraft", false);
                  handleSubmit(onsubmit)(e);
                }, 800)}
                disabled={isDisabled}
              />
            </div>
          </div>
        </>
      )}
    </div>
  );
};

export default StayDetailCreationForm;
